<template>
  <div class="owner-view">
    <header>
      <b-container class="px-1">
        <nav class="d-flex justify-content-between align-items-center">
            <img width="100px" src="@/assets/images/home/logo white.svg" alt="Logo">
            <div class="d-flex align-items-center lang_viewBtn">
                <b-button rounded size="sm" @click="Set_View('tenantView')">
                  <img width="20px" src="@/assets/images/icons/switch-user.svg">
                  <span>Tenant View</span>    
                </b-button>
                <NuxtLink class="mx-2 font-weight-bold" :to="switchLocalePath(availableLocale.code)">{{ $i18n.locale === 'ar' ? 'En' : 'Ar' }}</NuxtLink>
            </div>
        </nav>
      </b-container>
    </header>
    <div class="slider-container" :style="'background-image:url(&quot;' + require(`~/assets/images/home/bg.svg`) + '&quot;);'">
      <div class="new-ds-home">
        <!-- <p class="home-subtitle px-2">{{ $t('header.title') }} RentUp!</p> -->
        <p class="home-subtitle px-2">{{ dir === 'ltr' ? 'A New experience in Renting' : 'تجربة جديدة في الإيجار' }}</p>
        <!-- <p class="home-guide">{{ $t('header.post_nog_chat') }}</p> -->
        <p class="home-guide">{{ dir === 'ltr' ? 'You will find Tenant’s on our  Marketplace, see their requests & communicate DIRECTLY with them' : 'سوف تجد المستأجرين على المتجر الخاص بنا ، وترى طلباتهم تتواصل معهم مباشرة' }}</p>
      </div>
    </div>
    <div class="popularProperties">
      <b-container>
        <div class="card_header d-flex justify-content-between align-items-center font-weight-bold my-2">
          <h3 class="text-dark font-weight-bold">{{ dir === 'ltr' ? 'Rent Requests' : 'طلبات الإيجار' }}</h3>
          <p @click="() => this.$router.push(this.localePath('/marketplace'))" class="text-primary">{{ dir === 'ltr' ? 'view all' : 'عرض الكل' }}</p>
        </div>
        <div>
          <VueSlickCarousel class="isHome" v-if="requestsData && requestsData.length" :variableWidth="true" :centerMode="true" :centerPadding="'20px'" :arrows="false" :infinite="false" :initialSlide="1" :waitForAnimate="true" :draggable="true" :autoplay="false" :dots="false" :slidesToShow="1">
            <b-col cols="12" class="px-0" v-for="item in requestsData" :key="item.id">
              <MarketPlaceCard_2 :isSliderStyle="true" @click="() => this.$router.push(this.localePath('/marketplace'))" :shadow="true" :isHome="true" :item="item" />
            </b-col>
          </VueSlickCarousel>
        </div>
        <b-button variant="outline-primary" block class="mt-1 mb-3 text-center" :to="localePath('/marketplace')">Explore Requests in the Marketpace</b-button>
      </b-container>
    </div>
    <div class="categories px-2">
      <b-container class="makeGap">
        <h3 class="text-dark font-weight-bold">What do you want to rent ?</h3>
        <b-row>
          <b-col v-for="(category, index) in categories" cols="4" class="px-2 my-2" :key="index">
            <div class="categoryCard shadow d-flex align-items-center flex-column justify-content-center">
              <img :src="category.icon">
              <h5 class="text-center font-weight-semi-bold text-dark">{{ category.text }}</h5>
            </div>
          </b-col>
          <b-col cols="12" class="mt-4">
            <b-button variant="primary" class="text-center" block :to="localePath('/properties/add/1')">Add Unit</b-button>
          </b-col>
        </b-row>
      </b-container>
    </div>
  </div>
</template>

<script>
import MarketPlaceCard_2 from '@/components/marketplace/MarketPlaceCard_2.vue';
import MarketPlaceItem from '@/components/marketplace/MarketPlaceItem.vue';
import { createNamespacedHelpers } from 'vuex';
const { mapState, mapActions } = createNamespacedHelpers('HomeModule');
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'

export default {
  name: 'onboarding-slider',
  components:{
    MarketPlaceCard_2,
    MarketPlaceItem,
    VueSlickCarousel,
    SideBar: () => import('@/components/shared/AppHeader/SideBar.vue'),
  },
  data() {
    return {
      requestsData: [],
      showSideBar: false,
      categories: [
        { text: 'Residential', icon: require('@/assets/images/home/tags/residential.svg')},
        { text: 'Vacations', icon: require('@/assets/images/home/tags/vacations.svg')},
        { text: 'Administrative', icon: require('@/assets/images/home/tags/administrative.svg')},
        { text: 'Commertial', icon: require('@/assets/images/home/tags/commertial.svg')},
        { text: 'Events', icon: require('@/assets/images/home/tags/events.svg')},
        { text: 'Coworking Spaces', icon: require('@/assets/images/home/tags/co-working-spaces.svg')},
      ],
    };
  },
  computed:{
    ...mapState(['theView']),
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
    availableLocale() {
      return this.$i18n.locales.find((i) => i.code !== this.$i18n.locale);
    },
  },
  methods:{
    ...mapActions(['Set_View']),
    async getRequests() {
      const {Data} = await this.$axios.$get('/requests?per_page=3');
      this.requestsData = Data.collection
    },
  },
  mounted(){
    this.getRequests()
  }
};
</script>

<style lang="scss">
.owner-view {
  header {
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    z-index: 999999;
    color: #FFF;
    z-index: 3;
    h1 {
      // color: #fff;
      color: #0077e2;
      font-size: 2rem;
      font-weight: 500;
      span {
        color: #ffb625;
      }
    }
    .lang_viewBtn {
      gap: 5px;
      .btn {
        background-color: transparent;
        color: #FFF;
        border: 1px solid #FFC025;
        span {
          color: #FFC025 !important;
        }
      }
      & > a {
        color: #FFC025 !important;
      }
    }
  }
  .slick-slider.isHome {
    overflow: hidden;
    .slick-slide {
      width: 100% !important;
    }
  }
  .slider-container {
    position: relative;
    width: 100%;
    height: 35vh;
    background-position: center;
    background-size: cover;
    z-index: 0;
    padding-top: 70px !important;
    display: flex !important;
    align-content: space-between;
    justify-content: center;
    flex-wrap: wrap;
    .new-ds-home {
      position: relative !important;
      width: 100%;
      top: auto !important;
      bottom: auto !important;
      left: auto !important;
      right: auto !important;
      transform: translateY(75px);
    }
    .home-subtitle {
      font-weight: 700;
      font-size: 22px;
      line-height: 32px;
      text-align: center;
      color: #ffffff;
      position: relative;
      top: 160px;
    }
  
    .home-guide {
      font-weight: 500;
      font-size: 12px;
      line-height: 24px;
      text-align: center;
      color: #ffffff;
      padding: 0 25px;
    }
  }
  .categories {
    background-color: #F2F4F5;
    padding: 32px 8px 24px;
    // margin-bottom: 70px;
    .categoryCard {
      background-color: #FFF;
      gap: 15px;
      padding: 15px;
      border-radius: 15px;
      min-height: 112px;
      // h5 {
      //   color: #0077e2;
      // }
    }
  }
  
  .popularProperties {
    background-color: #FFF;
    padding: 15px 0;
    .card-body {
      padding: 10px !important;
    }
  }
  .icons-nav {
    .icon {
      flex-basis: 20% !important;
      p {
        font-size: 0.75em;
      }
      &.icon-plus {
        transform: translateY(-15px);
      }
    }
  }
}
</style>