<template>
  <div class="d-flex flex-column p-0 m-0">
    <div
      class="WelcomeHeader"
      :style="
        'background-image:url(&quot;' +
        require(`~/assets/images/home/bg.svg`) +
        '&quot;);'
      ">
      
      <p class="Content">
        <span>
          {{$t('header.title')}} Rent<span style="color: #ffc025">Up</span>!
        </span>
        <br />
        <!-- //change to arabic -->
        <span class="subtitle">{{$t('header.post_nog_chat')}}</span>
      </p>

      <div class="PostRequestButton" v-if="theView === 'ownerView'">
        <b-btn
          class="PostButt d-flex justify-content-center"
          :to="localePath('/properties/add/1')">
          {{ $t('header.listMyProp') }}
        </b-btn>
      </div>
      <div class="PostRequestButton" v-else>
        <b-btn
          class="PostButt d-flex justify-content-center"
          :to="localePath('/requests/post/property-category')">
          {{ $t('header.post_request_title') }}
        </b-btn>
        <!-- <nuxt-link :to="localePath('/search')">
          <p class="SearchPhrase">
            <svg-icon
              name="magnifying-glass"
