      <h2 class="text-center font-weight-bold">What do you want to rent?</h2>
      <b-container>
        <VueSlickCarousel class="mx-auto resizeSlider" :infinite="false" :waitForAnimate="false" :draggable="true" :autoplay="false" :dots="true" :slidesToShow="4" :rows="1" :slidesToScroll="2">
          <div class="eachOwlCarousel shadow p-3" v-for="(option, i) in categories" :key="i" :style="{'background-image' : `url(${option.imgSrc})`}">
            <h3 class="text-center text-dark font-weight-bold">{{ dir === 'ltr' ? option.title_en : option.title_ar }}</h3>
          </div>
        </VueSlickCarousel>
      </b-container>
    </div>
    <DahabCities1 v-if="theView === 'tenantView'" />

    <div class="requests text-center" v-if="theView === 'ownerView'">
      <template>
        <div class="cards_wrapper">
          <b-container>
            <div class="card_header d-flex justify-content-between align-items-center font-weight-bold my-2">
              <h2 class="h3 text-dark font-weight-bold mb-0">{{ dir === 'ltr' ? 'Rent Requests' : 'طلبات الإيجار' }}</h2>
              <!-- <p @click="() => this.$router.push(this.localePath('/marketplace'))" class="text-primary viewAll">{{  dir === 'ltr' ? 'view all' : 'عرض الكل'  }}</p> -->
            </div>
            
            <b-row class="justify-content-center">
              <b-col cols="12">
                <b-row>
                  <b-col @click="() =>$router.push(localePath('/marketplace'))" class="px-0" sm="6" lg="4" xl="3" v-for="item in requestsData" :key="item.id">
                    <MarketPlaceCard_2 :shadow="true" :isHome="true" :item="item" />
                  </b-col>
                </b-row>
              </b-col>
            </b-row>
          </b-container>
        </div>
      </template>
      <b-button variant="outline-primary" class="mx-auto my-3 text-center" :to="localePath('/marketplace')">{{ dir === 'ltr' ? 'Explore Requests in the Marketpace' : 'استكشف طلبات المتجر' }}</b-button>
    </div>

    <div class="pricing" v-if="theView === 'ownerView'">
      <b-container>
        <div class="content_header">
          <h3 class="h5">{{ dir === 'ltr' ? "Let's work together" : 'لنعمل معا' }}</h3>
          <h2 class="h2 font-weight-bold text-center">{{ dir === 'ltr' ? 'See our pricing' : 'ألق نظرة على أسعارنا' }}</h2>
          <p class="text-center font-size-semi-large mx-auto">{{dir === 'ltr' ? 'Connections to potential Tenants, Acquiring potential tenants, Increase profits, increase occupancy rates' : 'التواصل مع المستأجرين المحتملين ، اكتساب مستأجرين محتملين ، زيادة الأرباح ، زيادة معدلات الإشغال'}}</p>
        </div>
        <b-row class="my-4 justify-content-center">
          <b-col cols="12" v-for="n in 3" :key="n" sm="12" md="6" lg="4">
            <div class="card_price text-center mb-4">
              <div class="recommended" v-if="n === 2">{{ dir === 'ltr' ? 'Recommended' : 'موصى به' }}</div>
              <p class="text-center"><span :class="['budget', 'h3', 'font-weight-bold', dir === 'ltr' ? 'mr-2' : 'ml-2']">1</span> <sub><span class="currency h4">{{  dir === 'ltr' ? 'Free' : 'مجانية' }}</span></sub> </p>
              <p class="mb-1">Points: 1 Credits</p>
              <p class="advantages">Regular property ad</p>
              <ul class="list-unstyled px-0">
                <li class="mb-2 d-flex align-items-start"> <img :class="['mt-1', dir === 'ltr' ? 'mr-2' : 'ml-2']" width="15px" src="@/assets/images/home/done_mark.png" alt="Features"> <span class="text-primary">For sale listings valid for 6 months</span></li>
                <li class="d-flex align-items-start"> <img :class="['mt-1', dir === 'ltr' ? 'mr-2' : 'ml-2']" width="15px" src="@/assets/images/home/done_mark.png" alt="Features"> <span class="text-primary">The validity of the rental advertisement is 6 months</span></li>
              </ul>
              <b-button size="lg" class="mt-4" variant="primary">Get Started</b-button>
            </div>
          </b-col>
        </b-row>
      </b-container>
    </div>

  </div>
</template>

<script>

import MarketPlaceCard_2 from '@/components/marketplace/MarketPlaceCard_2.vue';
import SliderItem from './SliderItem.vue';
import MarketPlaceWidgetWeb from './MarketPlaceWidgetWeb';
import DahabCities1 from '~/components/home/DahabCities.vue';
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'

import { createNamespacedHelpers } from 'vuex';
const { mapState, mapActions } = createNamespacedHelpers('HomeModule');


export default {
  name: 'onboarding-web',
  components: {
    SliderItem,
    MarketPlaceWidgetWeb,
    DahabCities1,
    VueSlickCarousel,
    MarketPlaceCard_2,
    // carousel, //: typeof window !== 'undefined' ? () => import('vue-owl-carousel') : ''
  },
  data() {
    return {
      requestsData: [],
      settings: {
        dots: true,
        dotsClass: 'slick-dots custom-dot-class',
        edgeFriction: 0.35,
        infinite: false,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 3,
        rtl: this.$i18n.locale === 'en' ? false : true,
      },
      categories: [
        { title_en: 'Residential', title_ar: 'سكنى', imgSrc: require('@/assets/images/home/tags/residential.svg')},
        { title_en: 'Vacations', title_ar: 'أجازات', imgSrc: require('@/assets/images/home/tags/vacations.svg')},
        { title_en: 'Administration', title_ar: 'إدارى', imgSrc: require('@/assets/images/home/tags/administrative.svg')},
        { title_en: 'Commercial', title_ar: 'تجارى', imgSrc: require('@/assets/images/home/tags/commertial.svg')},
        { title_en: 'Events', title_ar: 'مناسبات', imgSrc: require('@/assets/images/home/tags/events.svg')},
        { title_en: 'Coworking Spaces', title_ar: 'أماكن للعمل الجماعى', imgSrc: require('@/assets/images/home/tags/co-working-spaces.svg')},
      ],
      // windowWidth: window.innerWidth,
    };
  },
  computed:{
    ...mapState(['theView']),
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    }
  },
  methods: {
    ...mapActions(['Set_View']),
    async getRequests(){
      if(window.innerWidth >= 992 && window.innerWidth < 1200){
        const { Data } = await this.$axios.$get('/requests?per_page=3');
        this.requestsData = Data.collection;
      }else {
        const { Data } = await this.$axios.$get('/requests?per_page=4');
        this.requestsData = Data.collection;
      }
    },
    getRequestsOnResize(){
      this.debounce()
    },
    debounce() {
      if (this.timeout)
        clearTimeout(this.timeout);

      this.timeout = setTimeout(() => {
        this.getRequests()
      }, 400);
    },
  },
  mounted(){
    this.$nextTick(() => {
      window.addEventListener('resize', this.getRequestsOnResize);
    }),
    this.getRequests();
    this.Set_View(localStorage.getItem('choosedUserView') || 'tenantView')
  },
};
</script>

<style lang="scss">
