    // transform: translateY(100px) !important;
    .subtitle {
      font-weight: 500;
      font-size: 18px;
      line-height: 24px;
    }
  }

  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.2);
    z-index: -1;
  }
}

.requests {
  padding: 20px 0 !important;
  .viewAll {
    cursor: pointer;
    font-size: 16px !important;
  }
}

.pricing {
