  background-color: #FFF2F2;
  .content_header {
    h3 {
      color: #FFC025 !important;
      text-align: center;
    }
    p {
      max-width: 550px;
    }
  }
  .card_price {
    padding: 30px;
    background-color: #FFF;
    border-radius: 1rem;
    position: relative;
    overflow: hidden;
    .recommended {
      position: absolute;
      left: 0;
      top:0;
      transform: rotate(-45deg) translateX(-92px) translateY(-65px);
      background-color: #FFC025;
      color: #FFF;
      padding: 10px;
      width: 300px;
      text-align:center
    }
    p:first-of-type {
      margin-bottom: 25px;
    }
    ul {
      li {
        span {
          font-size: 16px !important;
        }
      }
    }
  }
}

.PostRequestButton {
  margin-top: 2rem;
  // margin-bottom: 3rem;
  display: flex;
  justify-content: center;
  // align-items: center;

  .PostButt {
    background: #0078ff;
    border-radius: 8px;
    color: #ffffff;
    font-weight: 600;
    font-size: 18px;
    line-height: 16px;
    padding: 14px;
    width: 400px;
    border-color: transparent !important;
  }
  .SearchPhrase {
    margin-top: 1rem;
    border: 1px solid #ffffff;
    border-radius: 8px;
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    color: #ffffff;
    padding: 15px 75px;
  }
}
.Cards {
  overflow-x: hidden;
  margin-top: 60px;
  margin-bottom: 60px;

  .card {
    border: none;
    font-weight: bold;
    font-size: 20px;
    color: #112875;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    gap: 20px;
