<template>
  <main class="requests">
    <b-container class="h-25">
      <div class="mobile-puller"></div>
      <b-row class="mb-3">
        <b-col cols="12" class="mb-3">
          <div class="slider-title">
            <h5 class="text-left slider-title-text">
              <nuxt-link :to="localePath('/Dahab?term=0')">
                {{ $t('marketplace.widget_title') }}.</nuxt-link
              >
            </h5>
          </div>
        </b-col>
        <b-col cols="12">
          <VueSlickCarousel v-bind="settings" v-if="marketPlaceData.length > 0">
            <div v-for="(item, index) in marketPlaceData" :key="index">
              <div class="item-container mr-3">
                <div class="user-avatar">
                  <AppImage
                    :src="item.user_avatar ? item.user_avatar : ''"
                    class="user-image"
                  />
                </div>
                <h1 class="request-title">{{ item.name }}</h1>
                <div class="d-flex mt-2">
                  <div class="meta-box">
                    <svg-icon name="duration" height="24" width="24" />
                    <span>{{ item.duration }} </span>
                  </div>
                  <div class="ml-4 meta-box">
                    <svg-icon name="calendar" height="24" width="24" />
                    <span>
                      {{
                        $dateFns.format(
                          new Date(item.move_in_date),
                          'dd MMM yy'
                        )
                      }}</span
                    >
                  </div>
                </div>
                <p class="gray font-size-normal mt-2 mb-0 font-weight-bold">
                  {{ item.budget }} EGP
                  <span class="light-gray font-size-smaller font-weight-normal"
                    >/{{ $t('property.budget') }}</span
                  >
                </p>
              </div>
            </div>
          </VueSlickCarousel>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12">
          <nuxt-link class="view-all" :to="localePath('/marketplace')">
            {{ $t('marketplace.view_all_requests') }}.</nuxt-link
          >
        </b-col>
      </b-row>
    </b-container>
  </main>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import PropertyCard from '@/components/shared/PropertyCard'
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
import { fetchMarketPlaceData } from '@/api/marketplace'

export default {
  name: 'MarketPlaceWidget',
  components: {
    Swiper,
    SwiperSlide,
    PropertyCard,
    VueSlickCarousel,
  },
  data() {
    return {
      marketPlaceData: [],

      settings: {
        pauseOnHover: true,
        autoplaySpeed: 2000,
        autoplay: true,
        lazyLoad: 'ondemand',
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        rtl: this.$i18n.locale === 'en' ? false : true,

        slidesToScroll: 1,
        initialSlide: 0,
        responsive: [
          {
            breakpoint: 1024,
            settings: {
              slidesToShow: 1,
            },
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 1,
            },
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
            },
          },
        ],
      },
    }
  },

  created() {
    this.getMarketPlaceData()
  },
  methods: {
    async getMarketPlaceData() {
      try {
        let perPage = 5
        const response = await fetchMarketPlaceData(perPage)
        this.marketPlaceData = response.Data.data.data
      } catch (error) {}
    },
  },
}
</script>

<style scoped lang="scss">
.requests {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  .mobile-puller {
    width: 59px;
    height: 6px;
    left: 146.5px;
    top: 545px;
    background: linear-gradient(
        0deg,
        rgba(38, 45, 61, 0.32),
        rgba(38, 45, 61, 0.32)
      ),
      #ffffff;
    border-radius: 8px;
    margin: 10px auto;
  }
}
.slider-title {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: flex-start;
  .slider-title-text {
    font-weight: 700;
    font-size: 32px;
    line-height: 40px;
    color: #262d3d;
  }
}
.item-container {
  width: 262px;
  padding: 12px;
  /* Grays/Almost white */

  background: #fbfbfb;
  border-radius: 4px;

  .request-title {
    font-size: 14px;
    line-height: 16px;
    margin-top: 10px;
    color: #0f256e;
  }

  .meta-box {
    background: linear-gradient(
        0deg,
        rgba(38, 45, 61, 0.04),
        rgba(38, 45, 61, 0.04)
      ),
      #ffffff;
    border-radius: 8px;
    padding: 8px 12px;

    span {
      font-size: 12px;
      color: #0f256e;
    }
  }
  .user-avatar {
    position: relative;

    img {
      width: 48px;
      height: 48px;

      border-radius: 50%;
    }
  }
}

.view-all {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 12px 24px;
  border: 1px solid #204ecf;
  border-radius: 8px;
  color: #204ecf;
  font-weight: 600;
  font-size: 16px;
}
</style>
