<template>
  <div>
    <div class="onboardingMobile d-sm-block d-md-none p-0 m-0">
      <!-- <onboarding-slider /> -->
      <Onboarding_New v-if="theView === 'tenantView'" />
      <Onboarding_New_Owner v-else />
    </div>
    <div class="onboardingWeb d-none d-sm-none d-md-block">
      <onboarding-web />
    </div>
    <b-container>
      <b-modal hide-footer hide-header centered v-model="modalShow" class="d-flex flex-column" @click="test">
        <b-img src="@/assets/images/home/user_type.svg" fluid alt="Responsive image"></b-img>
        <p class="text-center font-weight-bold">I'm a</p>
        <div class="d-flex makeGap justify-content-center mt-4">
          <b-button size="md" variant="outline-primary" @click="changeView('tenantView')">Tenant</b-button>
          <b-button size="md" variant="outline-primary" @click="changeView('ownerView')">Landlord</b-button>
          <b-button size="md" variant="outline-primary" @click="changeView('ownerView')">Broker</b-button>
        </div>
      </b-modal>
    </b-container>
  </div>
</template>

<script>
import OnboardingSlider from '@/components/onboarding/Onboarding.vue'
import Onboarding_New from '@/components/onboarding/Onboarding_New.vue'
import Onboarding_New_Owner from '@/components/onboarding/Onboarding_New_Owner.vue'
import OnboardingWeb from '@/components/onboarding/Onboardingweb'
import { createNamespacedHelpers } from 'vuex';
const { mapState, mapActions } = createNamespacedHelpers('HomeModule');
const { mapState: userMapState } = createNamespacedHelpers('UserModule');

export default {
  name: 'onboarding',
  data() {
    return {
      list: [],
      modalShow: false,
      choosedView: '',
    }
  },
  components: {
    OnboardingSlider,
    OnboardingWeb,
    Onboarding_New,
    Onboarding_New_Owner,
  },
  computed:{
    ...mapState(['theView']),
    ...userMapState(['user']),
    appEnv() {
      return this.$config.APP_ENV
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
  },
  methods: {
    ...mapActions(['Set_View']),
    changeView(view){
      this.Set_View(view);
      this.modalShow = false;
      this.choosedView = view;
      localStorage.setItem('choosedUserView', view)
    },
    test(){
      alert('Clicked')
    }
  },
  head() {
    return {
      title: `${this.dir === 'ltr' ? 'Rentup | Home' : 'الرئيسية'} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? "Home Page" : "الصفحة الرئيسية",
        }
      ]
    }
  },
  watch: {
    $i18n: {
      handler(value) {
        if (process.client) {
          if (this.$i18n.locale === 'ar') {
            document.body.lang = 'ar';
            document.body.dir = 'rtl';
          } else {
            document.body.lang = 'en';
            document.body.dir = 'ltr';
          }
        }
      },
      immediate: true,
    },
    modalShow(newVal){
      if(!newVal){
        if(!this.choosedView){
          this.Set_View('tenantView');
          localStorage.setItem('choosedUserView', 'tenantView');
        }
      }
    },
  },
  mounted(){
    if(localStorage.getItem('choosedUserView') || this.user){
      this.modalShow = false;
      this.choosedView = localStorage.getItem('choosedUserView');
    }else{
      this.modalShow = true;
    }
    this.Set_View(localStorage.getItem('choosedUserView') || 'tenantView');
  }
}
</script>

<style lang="scss" scoped>
.makeGap {
  gap: 15px;
}
.onboardingMobile {
  margin-top: -70px !important;
  overflow-y: hidden;
}
[dir='rtl'] .onboardingMobile,
[dir='rtl'] .onboardingWeb {
  overflow-x: hidden;
}
</style>
