<template>
  <main class="requests">
    <b-container class="h-25">
      <div class="mobile-puller"></div>
      <b-row class="mb-3">
        <b-col cols="12" class="mb-3 mt-4">
          <div class="slider-title">
            <h5 class="text-left slider-title-text">
              <nuxt-link :to="localePath('/Dahab?term=0')">
                {{ $t('marketplace.widget_title') }}.</nuxt-link
              >
            </h5>
          </div>
        </b-col>
        <b-col cols="12 pt-4 ">
          <VueSlickCarousel v-bind="settings" v-if="marketPlaceData.length > 0">
            <div
              v-for="(item, index) in marketPlaceData"
              :key="index"
              class="d-flex justify-content-center"
            >
              <div class="item-container d-flex flex-column">
                <div class="user-avatar">
                  <AppImage
                    :src="item.user_avatar ? item.user_avatar : ''"
                    class="user-image"
                  />
                </div>
                <p class="request-title">{{ item.name }}</p>
                <span class="my-2 text-lg-start">{{ item.duration }} </span>
                <div class="dateColor">
                  <span> {{ $t('marketplace.Starting_at') }}</span>
                  <span>
                    {{
                      $dateFns.format(
                        new Date(item.move_in_date),
                        'dd MMM yyyy'
                      )
                    }}</span
                  >
                </div>
                <div class="dateColor">
                  <span> {{ $t('common.night1') }}</span>
                  <span> {{ item.budget }} {{ $t('common.egp') }}</span>
                </div>
              </div>
            </div>
          </VueSlickCarousel>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12">
          <div class="d-flex justify-content-center">
            <nuxt-link class="view-all" :to="localePath('/marketplace')">
              {{ $t('marketplace.view_all_requests') }}.</nuxt-link
            >
          </div>
        </b-col>
      </b-row>
    </b-container>
  </main>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import PropertyCard from '@/components/shared/PropertyCard'
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
import { fetchMarketPlaceData } from '@/api/marketplace'

export default {
  name: 'MarketPlaceWidgetWeb',
  components: {
    Swiper,
    SwiperSlide,
    PropertyCard,
    VueSlickCarousel,
  },
  data() {
    return {
      marketPlaceData: [],

      settings: {
        pauseOnHover: true,
        autoplaySpeed: 2000,
        autoplay: true,
        lazyLoad: 'ondemand',
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        rtl: this.$i18n.locale === 'en' ? false : true,

        slidesToScroll: 1,
        initialSlide: 0,
        responsive: [
          {
            breakpoint: 1025,
            settings: {
              slidesToShow: 2,
            },
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow:1,
            },
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
            },
          },
        ],
      },
    }
  },

  created() {
    this.getMarketPlaceData()
  },
  methods: {
    async getMarketPlaceData() {
      try {
        let perPage = 5
        const response = await fetchMarketPlaceData(perPage)
        this.marketPlaceData = response.Data.data.data
      } catch (error) {}
    },
  },
}
</script>

<style scoped lang="scss">
.requests {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  .mobile-puller {
    width: 59px;
