    left: 146.5px;
    top: 545px;
    background: linear-gradient(
        0deg,
        rgba(38, 45, 61, 0.32),
        rgba(38, 45, 61, 0.32)
      ),
      #ffffff;
    border-radius: 8px;
    margin: 10px auto;
  }
}
.slider-title {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  .slider-title-text {
    font-weight: 700;
    font-size: 40px;
    line-height: 40px;
    color: #09275b;
  }
}
.item-container {
  width: 262px;
  padding: 12px;
  /* Grays/Almost white */

  background: #ffffff;
  border-radius: 4px;

  .request-title {
    font-size: 32px;
    font-weight: 700;
    color: #09275b;
  }

  .meta-box {
    background: linear-gradient(
        0deg,
        rgba(38, 45, 61, 0.04),
        rgba(38, 45, 61, 0.04)
      ),
      #ffffff;
    border-radius: 8px;
    padding: 8px 12px;

    span {
      font-size: 12px;
      color: #0f256e;
    }
  }
  .user-avatar {
    position: relative;

    img {
      width: 120px;
      height: 120px;

      border-radius: 50%;
    }
  }
}

.view-all {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  padding: 12px 24px;
  border: none;
  border-bottom: 2px solid #204ecf;
   border-top: 2px solid #204ecf;

  color: #204ecf;
  font-weight: 600;
  font-size: 16px;
}
.dateColor {
  font-size: 22px;
  font-weight: 700;
  color: #09275b;
}
</style>
