<template>
  <main class="cities">
    <div class="mobile-puller"></div>
    <b-container class="h-100">
      <b-col cols="12" class="mb-3">
        <div class="slider-title">
          <h5 class="text-center slider-title-text">
            {{ $t('home.explore_dahab_cities') }}
          </h5>

          <span class="slider-description">{{ $t('home.see_more') }}</span>
        </div>
      </b-col>
      <b-col cols="12">
        <!-- <no-ssr>
          <carousel :autoplaySpeed="true" :center="true" :autoplay="true" :nav="false" :dots="false" :margin="10" :items="4" :loop="true">
            <div v-for="city in cities" :key="city.id">
              <div @click="goToProperties(city)">
                <div class="city-wrapper">
                  <div class="city-image">
                    <picture>
                      <source
                        :srcset="
                          require(`@/assets/images/cities/${city.image_name}.webp`)
                        "
                        type="image/webp"
                      />
                      <source
                        :srcset="
                          require(`@/assets/images/cities/${city.image_name}.jpg`)
                        "
                        type="image/jpg"
                      />
                      <img
                        :src="
                          require(`@/assets/images/cities/${city.image_name}.webp`)
                        "
                        :alt="city.image_name"
                      />
                    </picture>
                    <div class="city-title-wrapper">
                      <h2 class="city-title">{{ $t(`home.${city.name}`) }}</h2>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </carousel>
        </no-ssr> -->
        <VueSlickCarousel v-bind="settings">
            <div v-for="city in cities" :key="city.id">
              <div @click="goToProperties(city)">
                <div class="city-wrapper mx-0">
                  <div class="city-image">
                    <picture>
                      <source
                        :srcset="
                          require(`@/assets/images/cities/${city.image_name}.webp`)
                        "
                        type="image/webp"
                      />
                      <source
                        :srcset="
                          require(`@/assets/images/cities/${city.image_name}.jpg`)
                        "
                        type="image/jpg"
                      />
                      <img
                        :src="
                          require(`@/assets/images/cities/${city.image_name}.webp`)
                        "
                        :alt="city.image_name"
                      />
                    </picture>
                    <div class="city-title-wrapper">
                      <h2 class="city-title">{{ $t(`home.${city.name}`) }}</h2>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </VueSlickCarousel>
      </b-col>
      <b-col cols="12">
        <nuxt-link :to="localePath('/search')">
          <p class="SearchPhrase">
            <span class="SearchPhrase2">{{ $t('common.explore_all_properties') }}</span>
          </p>
        </nuxt-link>
      </b-col>
    </b-container>
  </main>
</template>

<script>
import SwiperClass, { Pagination } from 'swiper'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import { mapActions } from 'vuex'
import PropertyCard from '@/components/shared/PropertyCard'
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'

// import carousel from 'vue-owl-carousel'

export default {
  name: 'DahabCities',
  components: {
    Swiper,
    SwiperSlide,
    PropertyCard,
    VueSlickCarousel,
    // carousel, //: typeof window !== 'undefined' ? () => import('vue-owl-carousel') : ''
  },
  data() {
    return {
      properties: [],
      swiperOptionThumbs: {
        slidesPerView: 'auto',
        loopedSlides: 0,
        spaceBetween: 40,
        loop: false,
        touchRatio: 0.2,
        slideToClickedSlide: false,
        observer: true,
        observeParents: true,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        breakpoints: {
          1200: {
            slidesPerView: 6,
          },
          900: {
            slidesPerView: 2,
          },
          450: {
            slidesPerView: 1,
          },
        },
      },
      swiperOption: {
        slidesPerView: 3,
        spaceBetween: 30,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      },
      settings: {
        waitForAnimate: true,
        autoplaySpeed: 2000,
        autoplay: true,
        dots: false,
        infinite: false,
        speed: 500,
        slidesToScroll: 1,
        initialSlide: 0,
        draggable: true,
        slidesToShow: 4,
        responsive: [
          {
            breakpoint: 1024,
            settings: {
              slidesToShow: 4,
            },
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 2,
            },
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
            },
          },
        ],
      },
    }
  },

  computed: {
    cities() {
      return [
        { name: 'dahab', image_name: 'dahab', link: 'dahab', id: 30 },
        { name: 'north coast', image_name: 'north coast', link: 'north coast', id: 127 },
        {
          name: 'marsa matruh',
          image_name: 'marsa matruh',
          link: 'marsa matruh',
          id: 477
        },
        {
          name: 'giza',
          image_name: 'giza',
          link: 'giza',
          id: 4
        },
        {
          name: 'cairo',
          image_name: 'cairo',
          link: 'cairo',
          id: 2
        },
      ]
    },
  },
  methods:{
    ...mapActions(['setSearchLocationId']),
    goToProperties(city){
      localStorage.setItem('searchLocationId', JSON.stringify(city.id))
      this.setSearchLocationId(JSON.stringify(city.id))
      this.$router.push(this.localePath(`/properties/?keyword=${city.link}&term=0&location_id=${city.id}`))
    }
  }
}
</script>

<style scoped lang="scss">
.cities {
  vertical-align: middle;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  .mobile-puller {
    width: 100%;
    height: 1px;
    left: 146.5px;
    top: 545px;
    background: #e5e9eb;
    border-radius: 8px;
    margin: 0 0 60px 0;
  }
}
.slider-title {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: flex-start;
  text-align: center;
  .slider-title-text {
    font-weight: 700;
    font-size: 32px;
    line-height: 40px;
    color: #262d3d;
    margin: auto;
  }
}
.city-wrapper {
  margin: 0 15px;
  filter: drop-shadow(0px 4px 8px rgba(39, 39, 39, 0.08));
  @include media-breakpoint-down(sm) {
    margin: 0 4px;
  }
  .city-image {
    position: relative;

    img {
      width: 100%;
      height: 270px;

      background: linear-gradient(
          180deg,
          rgba(0, 0, 0, 0) 0%,
          rgba(0, 0, 0, 0.48) 86.35%
        ),
        #c4c4c4;
      border-radius: 8px;
    }
    .city-title-wrapper {
      position: relative;
      bottom: 0;
      width: 100%;
      margin: auto;
      z-index: 5;
      padding: 10px 12px;
      .city-title {
        text-transform: capitalize;
        opacity: 1;
        font-weight: 600;
        font-size: 14px;
        line-height: 18px;
        color: black;
        text-align: center;
      }
    }
  }
}
.slider-description {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: #48535b;
  margin: auto;
}

.SearchPhrase {
  border: 1px solid black;
  border-radius: 8px;
  font-weight: 600;
  font-size: 15px;
  line-height: 16px;
  color: black;
  padding: 15px 75px;
  width: 370px;
  max-width: 100%;
  height: 48px;
  text-align: center;
  margin: 1rem auto;
  &:hover {
    color: #0077e2;
  }
}
</style>